"use client";

import React, { useMemo, useState } from "react";

type RegionKey =
  | "Arica y Parinacota"
  | "Tarapacá"
  | "Antofagasta"
  | "Atacama"
  | "Coquimbo"
  | "Valparaíso"
  | "Metropolitana"
  | "O'Higgins"
  | "Maule"
  | "Ñuble"
  | "Biobío"
  | "La Araucanía"
  | "Los Ríos"
  | "Los Lagos"
  | "Aysén"
  | "Magallanes";

const ALL_REGIONS: RegionKey[] = [
  "Arica y Parinacota",
  "Tarapacá",
  "Antofagasta",
  "Atacama",
  "Coquimbo",
  "Valparaíso",
  "Metropolitana",
  "O'Higgins",
  "Maule",
  "Ñuble",
  "Biobío",
  "La Araucanía",
  "Los Ríos",
  "Los Lagos",
  "Aysén",
  "Magallanes",
];

type Product = {
  id: string;
  name?: string;
  description?: string;
  regions: RegionKey[];
  createdAt: string;
};

function cn(...xs: Array<string | false | null | undefined>) {
  return xs.filter(Boolean).join(" ");
}

function Icon({
  name,
  className,
}: {
  name: "box" | "bell" | "plus" | "search" | "chart";
  className?: string;
}) {
  const common = cn("h-5 w-5", className);
  switch (name) {
    case "box":
      return (
        <svg className={common} viewBox="0 0 24 24" fill="none">
          <path
            d="M21 8.5V15.5C21 18.5 19.5 20 16.5 20H7.5C4.5 20 3 18.5 3 15.5V8.5"
            stroke="currentColor"
            strokeWidth="1.8"
            strokeLinecap="round"
          />
          <path
            d="M21 8.5L12 13.5L3 8.5L12 3.8L21 8.5Z"
            stroke="currentColor"
            strokeWidth="1.8"
            strokeLinejoin="round"
          />
          <path d="M12 13.5V20" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" />
        </svg>
      );
    case "bell":
      return (
        <svg className={common} viewBox="0 0 24 24" fill="none">
          <path
            d="M12 22C13.3 22 14.4 21.2 14.8 20H9.2C9.6 21.2 10.7 22 12 22Z"
            fill="currentColor"
          />
          <path
            d="M18 8.5C18 6 16.4 4 14.1 3.3C13.8 2.6 13 2 12 2C11 2 10.2 2.6 9.9 3.3C7.6 4 6 6 6 8.5V12.2C6 12.7 5.8 13.2 5.4 13.6L4.3 14.7C3.7 15.3 4.1 16.4 4.9 16.4H19.1C19.9 16.4 20.3 15.3 19.7 14.7L18.6 13.6C18.2 13.2 18 12.7 18 12.2V8.5Z"
            stroke="currentColor"
            strokeWidth="1.8"
            strokeLinejoin="round"
          />
        </svg>
      );
    case "plus":
      return (
        <svg className={common} viewBox="0 0 24 24" fill="none">
          <path d="M12 5V19" stroke="currentColor" strokeWidth="1.9" strokeLinecap="round" />
          <path d="M5 12H19" stroke="currentColor" strokeWidth="1.9" strokeLinecap="round" />
        </svg>
      );
    case "search":
      return (
        <svg className={common} viewBox="0 0 24 24" fill="none">
          <path
            d="M11 19C15.4 19 19 15.4 19 11C19 6.6 15.4 3 11 3C6.6 3 3 6.6 3 11C3 15.4 6.6 19 11 19Z"
            stroke="currentColor"
            strokeWidth="1.8"
          />
          <path d="M21 21L16.6 16.6" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" />
        </svg>
      );
    case "chart":
      return (
        <svg className={common} viewBox="0 0 24 24" fill="none">
          <path d="M4 19V5" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" />
          <path d="M4 19H20" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" />
          <path d="M8 15V11" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" />
          <path d="M12 15V7" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" />
          <path d="M16 15V9" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" />
        </svg>
      );
  }
}

function StatCard({
  title,
  value,
  icon,
  tint,
}: {
  title: string;
  value: string | number;
  icon: React.ReactNode;
  tint: "blue" | "green" | "amber" | "purple";
}) {
  const tintCls = {
    blue: "bg-blue-50 text-blue-700 ring-blue-100",
    green: "bg-green-50 text-green-700 ring-green-100",
    amber: "bg-amber-50 text-amber-700 ring-amber-100",
    purple: "bg-purple-50 text-purple-700 ring-purple-100",
  }[tint];

  return (
    <div className="rounded-2xl border border-gray-200 bg-white p-5 shadow-sm">
      <div className="flex items-center gap-4">
        <div className={cn("grid h-11 w-11 place-items-center rounded-2xl ring-1", tintCls)}>{icon}</div>
        <div className="min-w-0">
          <div className="text-2xl font-semibold leading-tight text-gray-900">{value}</div>
          <div className="text-sm text-gray-500">{title}</div>
        </div>
      </div>
    </div>
  );
}

function Modal({
  open,
  onClose,
  children,
  title,
}: {
  open: boolean;
  onClose: () => void;
  children: React.ReactNode;
  title: string;
}) {
  if (!open) return null;
  return (
    <div className="fixed inset-0 z-50">
      <div className="absolute inset-0 bg-black/40" onClick={onClose} />
      <div className="absolute inset-0 grid place-items-center p-4">
        <div className="w-full max-w-2xl rounded-2xl bg-white shadow-xl ring-1 ring-black/5">
          <div className="flex items-center justify-between border-b border-gray-200 px-6 py-4">
            <div className="flex items-center gap-2">
              <span className="grid h-9 w-9 place-items-center rounded-xl bg-emerald-50 text-emerald-700 ring-1 ring-emerald-100">
                <Icon name="box" />
              </span>
              <div>
                <div className="text-base font-semibold text-gray-900">{title}</div>
                <div className="text-sm text-gray-500">Busca productos del Convenio Marco de Ferretería V2</div>
              </div>
            </div>

            <button
              onClick={onClose}
              className="rounded-xl px-3 py-2 text-gray-500 hover:bg-gray-100 hover:text-gray-800"
              aria-label="Cerrar"
            >
              ✕
            </button>
          </div>

          <div className="px-6 py-5">{children}</div>
        </div>
      </div>
    </div>
  );
}

export default function Page() {
  const [products, setProducts] = useState<Product[]>([]);
  const [query, setQuery] = useState("");
  const [modalOpen, setModalOpen] = useState(false);

  // Modal state
  const [id, setId] = useState("");
  const [name, setName] = useState("");
  const [desc, setDesc] = useState("");
  const [regions, setRegions] = useState<RegionKey[]>(["Metropolitana", "Valparaíso", "O'Higgins"]);
  const [searchInCM, setSearchInCM] = useState(true);

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase();
    if (!q) return products;
    return products.filter((p) => (p.id + " " + (p.name ?? "")).toLowerCase().includes(q));
  }, [products, query]);

  const stats = useMemo(() => {
    const total = products.length;
    const withPrices = 0;
    const alerts = 0;
    const regionsCount = 16;
    return { total, withPrices, alerts, regionsCount };
  }, [products]);

  function toggleRegion(r: RegionKey) {
    setRegions((prev) => (prev.includes(r) ? prev.filter((x) => x !== r) : [...prev, r]));
  }

  function selectAllRegions() {
    setRegions([...ALL_REGIONS]);
  }
  function selectNoneRegions() {
    setRegions([]);
  }

  function addProduct() {
    const cleanId = id.trim();
    if (!cleanId) return;

    // evita duplicado
    if (products.some((p) => p.id === cleanId)) {
      setModalOpen(false);
      return;
    }

    setProducts((prev) => [
      {
        id: cleanId,
        name: name.trim() || undefined,
        description: desc.trim() || undefined,
        regions: regions.length ? regions : ["Metropolitana"],
        createdAt: new Date().toISOString(),
      },
      ...prev,
    ]);

    // reset modal
    setId("");
    setName("");
    setDesc("");
    setRegions(["Metropolitana", "Valparaíso", "O'Higgins"]);
    setSearchInCM(true);
    setModalOpen(false);
  }

  const inputBase =
    "h-11 w-full rounded-xl border border-gray-200 bg-white px-4 text-sm text-gray-900 placeholder:text-gray-500 outline-none focus:border-emerald-400 focus:ring-4 focus:ring-emerald-100";

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Top bar */}
      <header className="sticky top-0 z-10 border-b border-gray-200 bg-white/80 backdrop-blur">
        <div className="mx-auto flex max-w-6xl items-center justify-between px-4 py-4">
          <div className="flex items-center gap-3">
            <div className="grid h-10 w-10 place-items-center rounded-2xl bg-emerald-600 text-white shadow-sm">
              <Icon name="chart" className="h-5 w-5" />
            </div>
            <div>
              <div className="text-lg font-semibold text-gray-900">Mercado Público</div>
              <div className="text-sm text-gray-500">Convenio Marco Ferretería</div>
            </div>
          </div>

          <div className="flex items-center gap-2">
            <button className="relative rounded-xl p-2 text-gray-600 hover:bg-gray-100">
              <Icon name="bell" />
              <span className="absolute -right-0.5 -top-0.5 h-2.5 w-2.5 rounded-full bg-emerald-500 ring-2 ring-white" />
            </button>

            <button
              onClick={() => setModalOpen(true)}
              className="inline-flex items-center gap-2 rounded-xl bg-emerald-600 px-4 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-emerald-700"
            >
              <Icon name="plus" className="h-4 w-4" />
              Agregar Producto
            </button>
          </div>
        </div>
      </header>

      <main className="mx-auto max-w-6xl px-4 py-8">
        {/* Stats */}
        <div className="grid grid-cols-1 gap-4 md:grid-cols-4">
          <StatCard title="Productos" value={stats.total} tint="blue" icon={<Icon name="box" />} />
          <StatCard title="Con precios" value={stats.withPrices} tint="green" icon={<Icon name="chart" />} />
          <StatCard title="Alertas activas" value={stats.alerts} tint="amber" icon={<Icon name="bell" />} />
          <StatCard title="Regiones" value={stats.regionsCount} tint="purple" icon={<Icon name="box" />} />
        </div>

        {/* Search */}
        <div className="mt-6 rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
          <div className="flex items-center gap-3">
            <div className="grid h-11 w-11 place-items-center rounded-2xl bg-gray-100 text-gray-700">
              <Icon name="search" />
            </div>
            <input
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              placeholder="Buscar por nombre o ID..."
              className={inputBase}
            />
          </div>
        </div>

        {/* Content */}
        <div className="mt-6 rounded-2xl border border-gray-200 bg-white shadow-sm">
          <div className="flex items-center justify-between border-b border-gray-200 px-5 py-4">
            <div className="text-sm font-semibold text-gray-900">Listado</div>
            <div className="text-xs text-gray-500">{filtered.length} resultado(s)</div>
          </div>

          {filtered.length === 0 ? (
            <div className="grid place-items-center px-6 py-14">
              <div className="grid place-items-center text-center">
                <div className="grid h-14 w-14 place-items-center rounded-2xl bg-gray-100 text-gray-600">
                  <Icon name="box" className="h-7 w-7" />
                </div>
                <div className="mt-4 text-base font-semibold text-gray-900">Sin productos</div>
                <div className="mt-1 text-sm text-gray-500">
                  Agrega tu primer producto para comenzar a monitorear precios
                </div>
                <button
                  onClick={() => setModalOpen(true)}
                  className="mt-6 inline-flex items-center gap-2 rounded-xl bg-emerald-600 px-4 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-emerald-700"
                >
                  <Icon name="plus" className="h-4 w-4" />
                  Agregar Producto
                </button>
              </div>
            </div>
          ) : (
            <div className="divide-y divide-gray-200">
              {filtered.map((p) => (
                <div key={p.id} className="flex items-center justify-between px-5 py-4">
                  <div className="min-w-0">
                    <div className="flex items-center gap-2">
                      <span className="rounded-lg bg-gray-100 px-2 py-1 text-xs font-semibold text-gray-700">
                        {p.id}
                      </span>
                      <span className="truncate text-sm font-semibold text-gray-900">{p.name ?? "—"}</span>
                    </div>
                    <div className="mt-1 flex flex-wrap items-center gap-2 text-xs text-gray-500">
                      <span>{p.regions.length} región(es)</span>
                      <span className="text-gray-300">•</span>
                      <span>Creado: {new Date(p.createdAt).toLocaleString()}</span>
                    </div>
                  </div>

                  <button
                    onClick={() => setProducts((prev) => prev.filter((x) => x.id !== p.id))}
                    className="rounded-xl border border-gray-200 px-3 py-2 text-sm font-semibold text-gray-700 hover:bg-gray-100"
                  >
                    Quitar
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>
      </main>

      {/* Modal */}
      <Modal open={modalOpen} onClose={() => setModalOpen(false)} title="Agregar Producto de Ferretería">
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
          {/* Left */}
          <div className="space-y-4">
            <div>
              <label className="text-sm font-semibold text-gray-800">ID del Producto</label>
              <div className="mt-2 flex gap-2">
                <input value={id} onChange={(e) => setId(e.target.value)} placeholder="Ej: 2165747" className={inputBase} />
                <button
                  type="button"
                  className="grid h-11 w-11 place-items-center rounded-xl border border-gray-200 text-gray-700 hover:bg-gray-100"
                  title="Buscar"
                  onClick={() => {
                    // placeholder: aquí luego llamamos a tu API para traer nombre/desc desde MP
                  }}
                >
                  <Icon name="search" />
                </button>
              </div>

              <label className="mt-2 flex items-center gap-2 text-sm font-medium text-gray-700">
                <input
                  type="checkbox"
                  checked={searchInCM}
                  onChange={(e) => setSearchInCM(e.target.checked)}
                  className="h-4 w-4 rounded border-gray-300 text-emerald-600 focus:ring-emerald-200"
                />
                Buscar en Convenio Marco Ferretería V2
              </label>
            </div>

            <div>
              <label className="text-sm font-semibold text-gray-800">Nombre del Producto</label>
              <input value={name} onChange={(e) => setName(e.target.value)} placeholder="Nombre del producto" className={cn("mt-2", inputBase)} />
            </div>

            <div>
              <label className="text-sm font-semibold text-gray-800">Descripción (opcional)</label>
              <input value={desc} onChange={(e) => setDesc(e.target.value)} placeholder="Breve descripción" className={cn("mt-2", inputBase)} />
            </div>
          </div>

          {/* Right */}
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <label className="text-sm font-semibold text-gray-800">Regiones a monitorear</label>
              <div className="flex items-center gap-3 text-sm">
                <button type="button" onClick={selectAllRegions} className="font-semibold text-gray-700 hover:text-emerald-700">
                  Todas
                </button>
                <button type="button" onClick={selectNoneRegions} className="font-semibold text-gray-700 hover:text-emerald-700">
                  Ninguna
                </button>
              </div>
            </div>

            <div className="h-56 overflow-auto rounded-xl border border-gray-200 p-3">
              <div className="grid grid-cols-2 gap-3">
                {ALL_REGIONS.map((r) => (
                  <label key={r} className="flex items-center gap-2 text-sm font-medium text-gray-700">
                    <input
                      type="checkbox"
                      checked={regions.includes(r)}
                      onChange={() => toggleRegion(r)}
                      className="h-4 w-4 rounded border-gray-300 text-emerald-600 focus:ring-emerald-200"
                    />
                    <span className="leading-tight">{r}</span>
                  </label>
                ))}
              </div>
            </div>

            <div className="flex items-center justify-end gap-2 pt-2">
              <button
                onClick={() => setModalOpen(false)}
                className="rounded-xl border border-gray-200 px-4 py-2.5 text-sm font-semibold text-gray-700 hover:bg-gray-100"
              >
                Cancelar
              </button>
              <button
                onClick={addProduct}
                className="rounded-xl bg-emerald-600 px-4 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-emerald-700"
              >
                Agregar
              </button>
            </div>
          </div>
        </div>
      </Modal>
    </div>
  );
}
